4. Banner
*********************************/

.banner
{
	width: 100%;
	margin-top: 30px;
}
.banner_item
{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 265px;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
}
.banner_category
{
	height: 50px;
	background: #FFFFFF;
	min-width: 180px;
	padding-left: 25px;
	padding-right: 25px;
}
.banner_category a
{
	display: block;
	color: #1e1e27;
	text-transform: uppercase;
	font-size: 22px;
	font-weight: 600;
	text-align: center;
	line-height: 50px;
	width: 100%;
	-webkit-transition: color 0.3s ease;
	transition: color 0.3s ease;
}
.banner_category a:hover
{
	color: #b5aec4;
}
.new_arrivals_sorting {
  display: inline-block;
  border-radius: 3px;
  margin-top: 59px;
  overflow: visible; }

.grid_sorting_button {
  height: 40px;
  min-width: 102px;
  padding-left: 25px;
  padding-right: 25px;
  cursor: pointer;
  border: solid 1px #ebebeb;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  background: #FFFFFF;
  border-radius: 3px;
  margin: 0;
  float: left;
  margin-left: -1px; }

.grid_sorting_button:first-child {
  margin-left: 0px; }

.grid_sorting_button.active:hover {
  background: #FE7C7F !important; }

.grid_sorting_button.active {
  color: #FFFFFF;
  background: #fe4c50; }

.DjMuZ4CnnVhh2oDwLxd7Q {
  width: 100%;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.DjMuZ4CnnVhh2oDwLxd7Q a {
  font-size: 12px;
  font-weight: 600; }

.hidden {
  visibility: hidden;
  opacity: 0; }

.product-item:hover .DjMuZ4CnnVhh2oDwLxd7Q {
  visibility: visible;
  opacity: 1; }

.product-grid {
  width: 100%;
  margin-top: 57px; }

.product-item {
  width: 20%;
  height: 380px;
  float: left;
  cursor: pointer; }

.product-item::after {
  display: block;
  position: absolute;
  top: 0;
  left: -1px;
  width: calc(100% + 1px);
  height: 100%;
  pointer-events: none;
  content: '';
  border: solid 2px rgba(235, 235, 235, 0);
  border-radius: 3px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.product-item:hover::after {
  box-shadow: 0 25px 29px rgba(63, 78, 100, 0.15);
  border: solid 2px #ebebeb; }

.product {
  width: 100%;
  height: 340px;
  border-right: solid 1px #e9e9e9; }

.product_image {
  width: 100%; }

.product_image img {
  width: 100%; }

.product_info {
  text-align: center;
  padding-left: 10px;
  padding-right: 10px; }

