.product_bubble {
  position: absolute;
  top: 15px;
  width: 50px;
  height: 22px;
  border-radius: 3px;
  text-transform: uppercase; }

.product_bubble span {
  font-size: 12px;
  font-weight: 600;
  color: #FFFFFF; }

.product_bubble_right::after {
  content: ' ';
  position: absolute;
  width: 0;
  height: 0;
  left: 6px;
  right: auto;
  top: auto;
  bottom: -6px;
  border: 6px solid; }

.product_bubble_left::after {
  content: ' ';
  position: absolute;
  width: 0;
  height: 0;
  left: auto;
  right: 6px;
  bottom: -6px;
  border: 6px solid; }

.product_bubble_red::after {
  border-color: transparent transparent transparent #fe4c50; }

.product_bubble_left::after {
  border-color: #51a042 #51a042 transparent transparent; }

.product_bubble_red {
  background: #fe4c50; }

.product_bubble_green {
  background: #51a042; }

.product_bubble_left {
  left: 15px; }

.product_bubble_right {
  right: 15px; }

.add_to_cart_button {
